/* ==========================================================================
	Styles
========================================================================== */
body {
  font-family: 'Brandon Grotesque';
}

.container {
  max-width: 960px;
  margin: 0 auto;
}

/*** Main Introduction **********************/
.main_intro {
  padding: 0 2em;
}

.main_intro_title {
  font-size: 36px;
  font-weight: 400;
  text-align: center;
}

.main_intro_link {
  color: #333;
  text-decoration: none;
}

.main_intro_link:hover {
  text-decoration: underline;
}

/* ==========================================================================
	Header
========================================================================== */
.main_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .5em 0 1em 1em;
}

.main_logo {
  width: 26px;
}

.main_logo_link {
  position: relative;
  display: inline-block;
  height: 30px;
}

.main_logo_link:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@media screen and (min-width: 600px) {
  .main_logo {
    width: 40px;
  }

  .main_logo_link {
    height: 47px;
  }

  .main_header {
    padding: 2em 1em 2em 2em;
  }
}
/* ==========================================================================
	Navigation
========================================================================== */
/*** Main Navigation **********************/
.main_nav_link-no_js {
  display: block;
}

.main_nav_list {
  display: none;
  list-style-type: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  background-color: black;
}

.main_nav_list_item_link {
  display: block;
  width: 100%;
  padding: 2em 0;
  text-align: center;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 2px;
}
.main_nav_list_item_link:hover {
  background-color: gray;
}

.main_nav_list_item_link_icon {
  display: none;
}

.js .main_nav-active .main_nav_list {
  display: block;
}

@media screen and (min-width: 600px) {
  .main_nav {
    position: absolute;
    z-index: 2;
    top: 1.6em;
    right: 2em;
  }

  .main_nav-stretch_bg {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    transition: height 0.2s;
  }

  .main_nav_list {
    position: relative;
    visibility: hidden;
    display: none;
    list-style-type: none;
    background-color: inherit;
    overflow-y: inherit;
    padding: 0 0 2em 0;
    margin: 0;
    z-index: 4;
  }

  .main_nav_list_item:hover:before {
    position: absolute;
    content: '';
    background-color: yellow;
    width: 5px;
    height: 48px;
  }

  .main_nav_list_item_link {
    position: relative;
    display: block;
    padding: 16px 22px;
    width: inherit;
  }
  .main_nav_list_item_link:hover {
    background-color: inherit;
  }

  .main_nav_list_item_link_icon {
    fill: white;
    width: 16px;
    display: block;
  }

  .main_nav_list_item_link_title {
    visibility: hidden;
    display: block;
    position: absolute;
    right: 60px;
    top: 0;
    color: black;
    opacity: .25;
    padding: 1em 1.5em;
    font-size: 14px;
    transition: right .25s;
    background-color: yellow;
    text-transform: lowercase;
  }

  .main_nav_list_item:hover .main_nav_list_item_link_title {
    visibility: visible;
    opacity: 1;
    right: 70px;
  }

  .js .main_nav-active .main_nav-stretch_bg, .no-js .main_nav:hover .main_nav-stretch_bg {
    background-color: black;
    height: 100%;
  }

  .js .main_nav-active .main_nav_list, .no-js .main_nav:hover .main_nav_list {
    visibility: visible;
    display: block;
  }
}
/*** Hamburger Icon **********************/
.main_nav_icon {
  cursor: pointer;
  width: 16px;
  padding: 22px;
  fill: black;
}

.main_nav_icon-top {
  transform-origin: 0px 19px;
}

.main_nav_icon-bottom {
  transform-origin: 0px 44px;
}

.main_nav_icon-top, .main_nav_icon-bottom {
  transition: transform .5s;
  transform: rotate(0);
}

.main_nav_icon-middle {
  transition: opacity .5s;
  opacity: 1;
}

.main_nav_close {
  text-align: right;
}

.main_nav_close_icon {
  fill: white;
  width: 16px;
  padding: 1em;
}

@media screen and (min-width: 600px) {
  .main_nav_icon {
    position: relative;
    z-index: 3;
  }

  .main_nav_close_icon {
    display: none;
  }

  .no-js .main_nav:hover .main_nav_icon-top, .main_nav-active .main_nav_icon-top {
    fill: white;
    transform: rotate(45deg);
  }

  .no-js .main_nav:hover .main_nav_icon-middle, .main_nav-active .main_nav_icon-middle {
    opacity: 0;
  }

  .no-js .main_nav:hover .main_nav_icon-bottom, .main_nav-active .main_nav_icon-bottom {
    fill: white;
    transform: rotate(-45deg);
  }

  .js .main_nav-active .main_nav_icon-top {
    transform: rotate(45deg);
  }

  .js .main_nav-active .main_nav_icon-bottom {
    transform: rotate(-45deg);
  }
}
/* ==========================================================================
	Footer
========================================================================== */
/*** Footer **********************/
.main_footer {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}

.footer_menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.footer_menu_item {
  text-align: center;
}

.footer_menu_item_link {
  text-decoration: none;
  display: block;
  width: 100%;
  padding: 1em 0;
  color: black;
}

.copyright_notice {
  font-size: 12px;
  text-align: center;
  padding: 3em 0 1em 0;
}

@media screen and (min-width: 600px) {
  .main_footer {
    flex-direction: row;
    justify-content: space-between;
    padding: 2em 1em 1em 1em;
  }

  .footer_menu {
    display: inline;
    width: auto;
  }

  .footer_menu_item, .footer_menu_item_title {
    display: inline;
  }

  .footer_menu_item_link {
    padding: 0 1em;
    display: inline;
  }

  .footer_menu_item_title {
    display: none;
    font-size: 18px;
  }

  .copyright_notice {
    padding: 0;
  }
}
/* ==========================================================================
	Helper Functions
========================================================================== */
/*** Helpers ********************************/
.no_scroll-sm_only {
  overflow-y: hidden;
}

@media screen and (min-width: 600px) {
  .no_scroll-sm_only {
    overflow-y: auto;
  }
}
